<template>
  <div class="about pa-3">
    <p class="text-left">Prueba por Mauricio olavarria.</p>
    <p class="text-center text-xl-h4 display-1">Borrador de listas de tareas</p>

    <p class="text-sm-left text-lg-h6 font-weight-light">
      Esta es la seccin donde explico de que se trata la aplicacin, es una
      prueba de Vuetify para crear aplicaciones web integrando las normativas de
      Material design, con la finalidad de poder crear poderosas y hermosas
      aplicaciones combinando diseo y animacin grfica.
    </p>

    <p class="text-right font-weight-thin">Hecho con Vuetify.</p>

    <!-- Insertar aca las animaciones con sus propiedads importante -->

    <v-row>
      <v-col sm-4
        ><lottie
          :options="alienOptions"
          :height="100"
          :width="100"
          v-on:animCreated="handleAnimation"
      /></v-col>
      <v-col sm-4
        ><lottie
          :options="spaceshipOptions"
          :height="150"
          :width="150"
          v-on:animCreated="handleAnimation"
      /></v-col>
      <v-col sm-4
        ><lottie
          :options="meteorOptions"
          :height="100"
          :width="100"
          v-on:animCreated="handleAnimation"
      /></v-col>
    </v-row>

    <lottie
      :options="oplogoAnimOptions"
      :height="400"
      :width="400"
      v-on:animCreated="handleAnimation"
    />

    <v-card outlined>
      <v-toolbar flat dense>
        <v-toolbar-title>
          <span class="subheading"
            >Velocidad de la animacion: multiplicada por
            {{ animationSpeed }}</span
          >
        </v-toolbar-title>
      </v-toolbar>
      <v-card-text>

        <v-slider
          :thumb-label="true"
          value="1"
          min="0"
          max="3"
          step="0.1"
          @change="onSpeedChange"
          v-model="animationSpeed"
        ><template v-slot:prepend>
          <v-icon
            color="primary"
            @click="playerManager"
          >
            {{ isPlaying ? 'mdi-pause' : 'mdi-play' }}
          </v-icon>
        </template>
        </v-slider
      ></v-card-text>
      <!-- <v-card-actions class="pb-3">
        <v-btn class="ml-3 mr-3" @click="stop">Parar</v-btn>
        <v-btn class="ml-3 mr-3" @click="pause">Pausar</v-btn>
        <v-btn class="ml-3 mr-3" @click="play">Reproducir</v-btn>
      </v-card-actions> -->
    </v-card>
  </div>
</template>


<script>
import Lottie from "vue-lottie";

import alienOptions from "../assets/alien.json";
import spaceshipOptions from "../assets/spaceship.json";
import meteorOptions from "../assets/meteor.json";

import oplogoAnimOptions from "../assets/oplogo.json";
export default {
  name: "app",
  components: {
    lottie: Lottie,
  },
  data() {
    return {
      oplogoAnimOptions: { animationData: oplogoAnimOptions },

      alienOptions: { animationData: alienOptions },
      spaceshipOptions: { animationData: spaceshipOptions },
      meteorOptions: { animationData: meteorOptions },

      animationSpeed: 1,
      isPlaying: true,
    };
  },
  methods: {
    playerManager: function(){
      if(this.isPlaying){
        this.pause();
        
      }else{
        this.play();
      }
      this.isPlaying = !this.isPlaying;
    },

    handleAnimation: function (anim) {
      this.anim = anim;
    },

    stop: function () {
      this.anim.stop();
    },

    play: function () {
      this.anim.play();
    },

    pause: function () {
      this.anim.pause();
    },

    onSpeedChange: function () {
      this.anim.setSpeed(this.animationSpeed);
    },
  },
};
</script>
